.main-header {
    width: 100%;
    height: 4rem;
    display: flex;
    align-items: center;
    position: relative;
    margin-bottom: 5rem;
    top: 0;
    left: 0;
    background: #00adb5;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.26);
    padding: 2 2rem;
    z-index: 0;
    text-align: center;
  }
  
  main {
    margin-top: 5rem;
  }
  
  @media (min-width: 768px) {
    .main-header {
      justify-content: space-between;
    }
  }